.layout {
  height: 100vh;

  display: grid;
  grid:
    "sidebar body" .5fr
    / auto .95fr;
  grid-gap: 8px;
  gap: 8px;
}

.sidebar {
  grid-area: sidebar;
  border-right: solid 1px black;
  height: 100vh;
  width: 5vw;
  background-color: #FFEB2F;
  position: fixed;
}

.body {
  grid-area: body;
  width: 95vw;
  height: 100vh;
  display: grid;
  grid-template-rows: repeat(auto-fill, auto);
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 8px;
  gap: 8px;
  margin-left: 5vw;
  position: center

}

